import { mapState } from 'vuex';
export default {
	computed:{
		...mapState(["address"]) 
	},
	mounted(){
		uni.setNavigationBarTitle({
		　　title:'管理家庭'
		})
	},
	methods:{
		modify(id){
			uni.navigateTo({
				url:'./modifyAddress' + '?id=' + id
			})
		},
		remove(id){
			this.$store.commit("removeAddress", id);
		},
		create(){
			uni.navigateTo({
				url:'./createAddress'
			})
		}
	}
}
